<div class="main_visual">
<svg viewBox="0 0 600 600">
<path d="M295.5,1a296.682,296.682,0,0,0-59.353,5.983A292.879,292.879,0,0,0,130.843,51.3a295.363,295.363,0,0,0-106.7,129.572,293.008,293.008,0,0,0-17.16,55.279,297.381,297.381,0,0,0,0,118.706A292.879,292.879,0,0,0,51.3,460.157a295.363,295.363,0,0,0,129.572,106.7,293.008,293.008,0,0,0,55.279,17.16,297.381,297.381,0,0,0,118.706,0,292.879,292.879,0,0,0,105.3-44.312,295.363,295.363,0,0,0,106.7-129.572,293.007,293.007,0,0,0,17.16-55.279,297.381,297.381,0,0,0,0-118.706,292.879,292.879,0,0,0-44.312-105.3,295.363,295.363,0,0,0-129.572-106.7,293.007,293.007,0,0,0-55.279-17.16A296.682,296.682,0,0,0,295.5,1m0-1C458.7,0,591,132.3,591,295.5S458.7,591,295.5,591,0,458.7,0,295.5,132.3,0,295.5,0Z" />
<g id="group"></g>
</svg>
</div>
</template>

<script>
import gsap from "gsap";
import chroma from "chroma-js";
export default {
    name: "Home",
    mounted() {
        const paths = document.querySelectorAll('path');
        const group = document.querySelector('#group');
        const colours = [
        chroma.scale(['#122538', '#2C3E50', '#395B7C', '#2F6A80', '#748489', '#A8B4B9'])];
        let currentGradient = 1;
        const tl = gsap.timeline({
        onReverseComplete : () => {
            tl.timeScale(1);
            tl.play(0);
        },
        onComplete: () => {
            tl.timeScale(1.5);
            tl.reverse(0);
        }
        });
        function generatePoints () {
        tl.clear();
        group.innerHTML = '';
        let delay = 0;
        paths.forEach(path => {
            const length = path.getTotalLength();
            for (let i = 0; i < length; i+=1) {
            const pointLength = Math.random() * length;
            const point = path.getPointAtLength(pointLength);
            const circle = document.createElementNS('http://www.w3.org/2000/svg', 'circle');
            circle.setAttribute('cx', point.x);
            circle.setAttribute('cy', point.y);
            circle.setAttribute('r', Math.random() * 3 + 1);
            group.appendChild(circle);
            const coloursX = (point.x / 476.5) + ((Math.random() - 0.5) * 0.2);
            tl.to(circle, {
                autoRound: false,
                fill: colours[currentGradient % colours.length](coloursX).hex(),
                cx: point.x + (Math.random() - 0.5) * 60,
                cy: point.y + (Math.random() - 0.5) * 60,
                duration: 'random(0.5, 2)',
                delay: (delay + pointLength) * 0.002,
                ease: 'power2.out'
            }, 0);
            }
            delay += length;
        });
        tl.reversed(false).play(0);
        currentGradient++;
        }
        generatePoints();
    },
};
</script>

